.TF_outter_container {
    width: 100%;
    height: 1500px;
    background: var(--unnamed-color-f6f6f6) 0% 0% no-repeat padding-box;
    background: #f6f6f6 0% 0% no-repeat padding-box;
    opacity: 1;
    .TF_inner_container {
        .TF_headers {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;

            h2 {
                margin-top: 2em;
                font: var(--unnamed-font-style-normal) normal
                    var(--unnamed-font-weight-600) var(--unnamed-font-size-35) /
                    var(--unnamed-line-spacing-58)
                    var(--unnamed-font-family-montserrat);
                letter-spacing: var(--unnamed-character-spacing-0);
                color: var(--unnamed-color-161616);
                text-align: center;
                font: normal normal 600 35px/58px Montserrat;
                letter-spacing: 0px;
                color: #161616;
                opacity: 1;
            }
            p {
                width: 997px;
                font: var(--unnamed-font-style-normal) normal
                    var(--unnamed-font-weight-normal)
                    var(--unnamed-font-size-18) / var(--unnamed-line-spacing-30)
                    var(--unnamed-font-family-montserrat);
                letter-spacing: var(--unnamed-character-spacing-0-27);
                text-align: center;
                font: normal normal normal 18px/30px Montserrat;
                letter-spacing: 0.27px;
                color: #696969;
                opacity: 1;
            }
        }
        .TF_wrapper {
            .TF_inner_wrapper {
                display: grid;
                grid-template-columns: 60% 40%;
                height: auto;
                grid-template-rows: auto;
                grid-column-gap: 40px;
                margin-top: 3em;
                .TF_wrapper_left {
                    height: 981px;
                    background: var(--unnamed-color-ffffff) 0% 0% no-repeat
                        padding-box;
                    background: #ffffff 0% 0% no-repeat padding-box;
                    box-shadow: 0px 3px 12px #16161608;
                    opacity: 1;
                    overflow: auto;
                    .TF_wrapper_content {
                        padding: 3em 5em;

                        h2 {
                            margin: 1em 0;
                            font: var(--unnamed-font-style-normal) normal
                                var(--unnamed-font-weight-bold)
                                var(--unnamed-font-size-45) /
                                var(--unnamed-line-spacing-55)
                                var(--unnamed-font-family-cormorant-garamond);
                            letter-spacing: var(
                                --unnamed-character-spacing--0-45
                            );
                            color: var(--unnamed-color-161616);
                            text-align: left;
                            font: normal normal bold 45px/55px Cormorant
                                Garamond;
                            letter-spacing: -0.45px;
                            color: #161616;
                            opacity: 1;
                        }
                        .TF_form {
                            form {
                                h3 {
                                    margin: 0 0 0.5em 0;

                                    text-align: left;
                                    letter-spacing: 0px;
                                    color: #626262;
                                    opacity: 1;
                                    font-size: 28px;
                                    font-family: Montserrat;
                                    font-weight: 500;
                                }
                                .TF_inner_form {
                                    .TF_wrapper_input {
                                        display: flex;
                                        align-items: center;
                                        flex-wrap: wrap;

                                        h4 {
                                            font: var(
                                                    --unnamed-font-style-normal
                                                )
                                                normal
                                                var(
                                                    --unnamed-font-weight-normal
                                                )
                                                var(--unnamed-font-size-20) /
                                                var(--unnamed-line-spacing-58)
                                                var(
                                                    --unnamed-font-family-montserrat
                                                );
                                            letter-spacing: var(
                                                --unnamed-character-spacing-0
                                            );
                                            color: var(--unnamed-color-2c2c2c);
                                            text-align: left;
                                            font: normal normal normal 20px/58px
                                                Montserrat;
                                            letter-spacing: 0px;
                                            color: #2c2c2c;
                                            opacity: 1;
                                        }
                                        .inner_input {
                                            display: flex;
                                            width: 100%;
                                            input {
                                                height: 45px;
                                                width: 100%;
                                                border: 1.5px solid
                                                    var(--unnamed-color-bcbcbc);
                                                border: 2px solid #bcbcbc;
                                                border-radius: 30px;
                                                opacity: 1;
                                                padding: 1em 1.5em;
                                            }
                                            input::placeholder {
                                                font-size: 18px;
                                                font-family: Montserrat;
                                            }
                                            input:focus {
                                                outline: none;
                                            }
                                            img {
                                                position: relative;
                                                right: 50px;
                                            }
                                        }
                                        .TF_input {
                                            margin: 1em 0;
                                            display: flex;
                                            align-items: center;
                                            justify-content: space-between;
                                            width: 100%;
                                            input {
                                                height: 45px;
                                                width: 48%;
                                                border: 1.5px solid
                                                    var(--unnamed-color-bcbcbc);
                                                border: 2px solid #bcbcbc;
                                                border-radius: 30px;
                                                opacity: 1;
                                                padding: 1em 1.5em;
                                            }
                                            .inner_input {
                                                display: flex;
                                                width: 100%;
                                                input {
                                                    height: 45px;
                                                    width: 100%;
                                                    border: 1.5px solid
                                                        var(
                                                            --unnamed-color-bcbcbc
                                                        );
                                                    border: 2px solid #bcbcbc;
                                                    border-radius: 30px;
                                                    opacity: 1;
                                                    padding: 1em 1.5em;
                                                }
                                                input::placeholder {
                                                    font-size: 18px;
                                                    font-family: Montserrat;
                                                }
                                                input:focus {
                                                    outline: none;
                                                }
                                                img {
                                                    position: relative;
                                                    right: 50px;
                                                }
                                            }
                                        }
                                    }
                                }
                                .TF_remember_left {
                                    display: flex;
                                    align-items: center;
                                    width: 100%;

                                    .TF_check {
                                        margin: 0.5em;
                                        padding: 0;
                                    }
                                    input {
                                        width: 15px;
                                        height: 15px;
                                        border: 1.25px solid
                                            var(--unnamed-color-bcbcbc);
                                        border: 1px solid #bcbcbc;
                                        opacity: 1;
                                    }
                                    label {
                                        margin-left: 0.5em;
                                        font-size: 15px;
                                        padding: 0 0 0 0em;
                                        color: #898989;
                                        font: var(--unnamed-font-style-normal)
                                            normal 300 15px /
                                            var(--unnamed-line-spacing-42)
                                            var(
                                                --unnamed-font-family-montserrat
                                            );
                                        letter-spacing: var(
                                            --unnamed-character-spacing-0
                                        );
                                        color: var(--unnamed-color-898989);
                                        text-align: left;
                                        font: normal normal 300 15px/42px
                                            Montserrat;
                                        letter-spacing: 0px;
                                        color: #898989;
                                        position: relative;
                                        left: 0px;
                                    }
                                }
                                .TF_button {
                                    margin-top: 3em;
                                    display: flex;
                                    align-items: center;
                                    justify-content: space-between;
                                    button {
                                        display: flex;
                                        align-items: center;
                                        justify-content: center;
                                        width: 280px;
                                        height: 52px;
                                        background: var(--unnamed-color-53bfd2)
                                            0% 0% no-repeat padding-box;
                                        background: #53bfd2 0% 0% no-repeat
                                            padding-box;
                                        opacity: 1;
                                        outline: none;
                                        border: none;
                                        font: var(--unnamed-font-style-normal)
                                            normal
                                            var(--unnamed-font-weight-bold)
                                            var(--unnamed-font-size-18) /
                                            var(--unnamed-line-spacing-22)
                                            var(
                                                --unnamed-font-family-montserrat
                                            );
                                        color: var(--unnamed-color-ffffff);
                                        text-align: right;
                                        font: normal normal bold 18px/22px
                                            Montserrat;
                                        letter-spacing: 0.45px;
                                        color: #ffffff;
                                        text-transform: uppercase;
                                    }
                                    h5 {
                                        text-decoration: underline;
                                        letter-spacing: 0px;
                                        color: #53bfd2;
                                        letter-spacing: 0px;
                                        font-size: 15px;
                                        font-family: Montserrat;
                                        font-weight: 600;
                                        cursor: pointer;
                                    }
                                }
                            }
                        }
                    }
                }
                .TF_wrapper_right {
                    height: 981px;
                    background: var(--unnamed-color-ffffff) 0% 0% no-repeat
                        padding-box;
                    background: #ffffff 0% 0% no-repeat padding-box;
                    box-shadow: 0px 3px 12px #16161608;
                    opacity: 1;
                    .TF_right {
                        height: auto;
                        padding: 4em 2em;

                        border-radius: 30px;

                        background: var(--unnamed-color-ffffff) 0% 0% no-repeat
                            padding-box;
                        background: #ffffff 0% 0% no-repeat padding-box;
                        box-shadow: 0px 3px 12px #16161608;
                        opacity: 1;
                        .TF_profile {
                            display: flex;
                            align-items: center;
                            img {
                                width: 92px;
                                height: 92px;
                                border-radius: 50px;
                            }
                            .TF_name {
                                margin-left: 1em;
                                h2 {
                                    font: var(--unnamed-font-style-normal)
                                        normal var(--unnamed-font-weight-bold)
                                        var(--unnamed-font-size-35) / 45px
                                        var(
                                            --unnamed-font-family-cormorant-garamond
                                        );
                                    color: var(--unnamed-color-161616);
                                    text-align: left;
                                    font: normal normal bold 35px/45px Cormorant
                                        Garamond;
                                    letter-spacing: -0.35px;
                                    color: #161616;
                                    opacity: 1;
                                }
                                p {
                                    font: var(--unnamed-font-style-normal)
                                        normal var(--unnamed-font-weight-medium)
                                        var(--unnamed-font-size-18) /
                                        var(--unnamed-line-spacing-22)
                                        var(--unnamed-font-family-montserrat);
                                    letter-spacing: var(
                                        --unnamed-character-spacing-0-9
                                    );
                                    color: var(--unnamed-color-898989);
                                    text-align: left;
                                    font: normal normal medium 18px/22px
                                        Montserrat;
                                    letter-spacing: 0.9px;
                                    color: #898989;
                                    text-transform: uppercase;
                                    opacity: 1;
                                }
                            }
                        }
                        .TF_wrapper {
                            .TF_details {
                                margin-top: 2em;
                                h3 {
                                    font: var(--unnamed-font-style-normal)
                                        normal var(--unnamed-font-weight-medium)
                                        15px / var(--unnamed-line-spacing-22)
                                        var(--unnamed-font-family-montserrat);
                                    color: var(--unnamed-color-898989);
                                    text-align: left;
                                    font: normal normal medium 15px/22px
                                        Montserrat;
                                    letter-spacing: 0.75px;
                                    color: #898989;
                                    text-transform: uppercase;
                                    opacity: 1;
                                }
                                .TF_data_inner {
                                    display: flex;
                                    align-items: center;
                                    h4 {
                                        margin-left: 1em;
                                        letter-spacing: 0px;
                                        color: #161616;
                                        opacity: 1;
                                        font-size: 22px;
                                        font-weight: 600;
                                        font-family: Montserrat;
                                    }
                                }
                            }

                            .TF_service_details {
                                margin-top: 2em;
                                .TF_service_wrapper {
                                    width: 100%;
                                    .TF_service_left {
                                        display: flex;
                                        justify-content: flex-start;
                                        .TF_service_header {
                                            width: 100%;
                                            .TF_inner_header {
                                                display: flex;
                                                align-items: center;
                                                justify-content: space-between;
                                                h3 {
                                                    height: 35px;
                                                    font-family: Montserrat;
                                                    font-size: 20px;
                                                    font-weight: 600;
                                                    letter-spacing: 0px;
                                                    color: #626262;
                                                    opacity: 1;
                                                }
                                            }

                                            .TF_data_item {
                                                .TF_data_title {
                                                    h3 {
                                                        font: var(
                                                                --unnamed-font-style-normal
                                                            )
                                                            normal
                                                            var(
                                                                --unnamed-font-weight-600
                                                            )
                                                            var(
                                                                --unnamed-font-size-20
                                                            ) /
                                                            var(
                                                                --unnamed-line-spacing-32
                                                            )
                                                            var(
                                                                --unnamed-font-family-montserrat
                                                            );
                                                        letter-spacing: var(
                                                            --unnamed-character-spacing-0-3
                                                        );
                                                        color: var(
                                                            --unnamed-color-2c2c2c
                                                        );
                                                        text-align: left;
                                                        font: normal normal 600
                                                            20px/32px Montserrat;
                                                        letter-spacing: 0.3px;
                                                        color: #898989;
                                                        opacity: 1;
                                                        cursor: pointer;
                                                        margin-top: 0.5em;
                                                    }
                                                    .line {
                                                        margin-top: 0.5em;
                                                        height: 2px;
                                                        width: 80% !important;
                                                        background-color: #bcbcbc;
                                                    }
                                                    .line.show {
                                                        display: none;
                                                    }
                                                }
                                                .TF_DT_grid {
                                                    display: grid;
                                                    grid-template-columns: 50% 50%;
                                                    grid-template-rows: auto;
                                                    align-items: center;
                                                    justify-content: space-between;
                                                    .TF_DT_left {
                                                        .TF_DT_content {
                                                            overflow: hidden;
                                                            transition: all 0.5s
                                                                cubic-bezier(
                                                                    0,
                                                                    1,
                                                                    0,
                                                                    1
                                                                );
                                                            max-height: 0;
                                                            p {
                                                                font: var(
                                                                        --unnamed-font-style-normal
                                                                    )
                                                                    normal
                                                                    var(
                                                                        --unnamed-font-weight-normal
                                                                    )
                                                                    var(
                                                                        --unnamed-font-size-20
                                                                    ) /
                                                                    var(
                                                                        --unnamed-line-spacing-58
                                                                    )
                                                                    var(
                                                                        --unnamed-font-family-montserrat
                                                                    );
                                                                letter-spacing: var(
                                                                    --unnamed-character-spacing-0
                                                                );
                                                                color: var(
                                                                    --unnamed-color-898989
                                                                );
                                                                text-align: left;
                                                                font: normal
                                                                    normal
                                                                    normal 20px/58px
                                                                    Montserrat;
                                                                letter-spacing: 0px;
                                                                color: #898989;
                                                                transition: 0.2s
                                                                    ease-in-out;
                                                                opacity: 1;
                                                            }
                                                            p:hover {
                                                                color: black;
                                                            }
                                                        }
                                                        .TF_DT_contentshow {
                                                            width: 100%;
                                                            margin: 0;
                                                            padding: 0;
                                                            max-height: 999px;
                                                            transition: all 0.5s
                                                                cubic-bezier(
                                                                    1,
                                                                    1,
                                                                    1,
                                                                    0
                                                                );
                                                            p {
                                                                margin: 0;
                                                                padding: 0;
                                                                font: var(
                                                                        --unnamed-font-style-normal
                                                                    )
                                                                    normal
                                                                    var(
                                                                        --unnamed-font-weight-normal
                                                                    )
                                                                    var(
                                                                        --unnamed-font-size-20
                                                                    ) /
                                                                    var(
                                                                        --unnamed-line-spacing-58
                                                                    )
                                                                    var(
                                                                        --unnamed-font-family-montserrat
                                                                    );
                                                                letter-spacing: var(
                                                                    --unnamed-character-spacing-0
                                                                );
                                                                color: var(
                                                                    --unnamed-color-898989
                                                                );
                                                                text-align: left;
                                                                font: normal
                                                                    normal
                                                                    normal 20px/58px
                                                                    Montserrat;
                                                                letter-spacing: 0px;
                                                                color: #898989;
                                                                opacity: 1;
                                                                width: 100%;
                                                            }

                                                            div {
                                                                height: 2px;
                                                                width: 80% !important;
                                                                background-color: #bcbcbc;
                                                            }
                                                        }
                                                    }
                                                    .TF_DT_right {
                                                        .TF_DT_content {
                                                            overflow: hidden;
                                                            transition: all 0.5s
                                                                cubic-bezier(
                                                                    0,
                                                                    1,
                                                                    0,
                                                                    1
                                                                );
                                                            max-height: 0;
                                                            p {
                                                                font: var(
                                                                        --unnamed-font-style-normal
                                                                    )
                                                                    normal
                                                                    var(
                                                                        --unnamed-font-weight-normal
                                                                    )
                                                                    var(
                                                                        --unnamed-font-size-20
                                                                    ) /
                                                                    var(
                                                                        --unnamed-line-spacing-58
                                                                    )
                                                                    var(
                                                                        --unnamed-font-family-montserrat
                                                                    );
                                                                letter-spacing: var(
                                                                    --unnamed-character-spacing-0
                                                                );
                                                                color: var(
                                                                    --unnamed-color-898989
                                                                );
                                                                text-align: left;
                                                                font: normal
                                                                    normal
                                                                    normal 20px/58px
                                                                    Montserrat;
                                                                letter-spacing: 0px;
                                                                color: #898989;
                                                                opacity: 1;
                                                            }
                                                        }
                                                        .TF_DT_contentshow {
                                                            height: auto;

                                                            width: 100%;
                                                            margin: 0;
                                                            padding: 0;
                                                            max-height: 999px;
                                                            transition: all 0.5s
                                                                cubic-bezier(
                                                                    1,
                                                                    1,
                                                                    1,
                                                                    0
                                                                );
                                                            p {
                                                                text-align: right;
                                                                margin: 0;
                                                                padding: 0;

                                                                font: var(
                                                                        --unnamed-font-style-normal
                                                                    )
                                                                    normal
                                                                    var(
                                                                        --unnamed-font-weight-normal
                                                                    )
                                                                    var(
                                                                        --unnamed-font-size-20
                                                                    ) /
                                                                    var(
                                                                        --unnamed-line-spacing-58
                                                                    )
                                                                    var(
                                                                        --unnamed-font-family-montserrat
                                                                    );
                                                                letter-spacing: var(
                                                                    --unnamed-character-spacing-0
                                                                );
                                                                color: var(
                                                                    --unnamed-color-898989
                                                                );
                                                                font: normal
                                                                    normal
                                                                    normal 20px/58px
                                                                    Montserrat;
                                                                letter-spacing: 0px;
                                                                color: #898989;
                                                                opacity: 1;
                                                                width: 100%;
                                                            }

                                                            div {
                                                                height: 2px;
                                                                width: 80% !important;
                                                                background-color: #bcbcbc;
                                                            }
                                                        }
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .TF_skip {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 100%;
                margin: 4em 0 3em 0;
                h2 {
                    text-align: center;
                    letter-spacing: 0px;
                    color: #222222;
                    font-family: Montserrat;
                    font-size: 22px;
                    margin: 0;
                }
                a {
                    display: flex;
                    align-items: center;
                    text-decoration: none;
                    text-align: center;
                    margin-left: 1em;
                    font: var(--unnamed-font-style-normal) normal
                        var(--unnamed-font-weight-bold)
                        var(--unnamed-font-size-18) /
                        var(--unnamed-line-spacing-22)
                        var(--unnamed-font-family-montserrat);
                    color: var(--unnamed-color-161616);
                    text-align: right;
                    font: normal normal bold 18px/22px Montserrat;
                    letter-spacing: 0.45px;
                    color: #161616;
                    text-transform: uppercase;
                }
            }
        }
    }
}
